import React, { useState, useEffect } from 'react';
import Filter from './components/Filter';
import PersonForm from './components/PersonForm';
import Persons from './components/Persons';
import Notification from './components/Notification';
import personService from './services/persons';

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState('');
  const [newNumber, setNewNumber] = useState('');
  const [searchName, setSearchName] = useState('');
  const [successMessage, setSuccessMessage] = useState(null);
  const [errorMessage, setErrorMessage] = useState(null);

  useEffect(() => {
    personService.getAll().then((initialPersons) => setPersons(initialPersons));
  }, []);

  const addName = (event) => {
    event.preventDefault();
    // check if the name already exists, return true if found
    const nameCheck = persons.some((person) => person.name === newName);

    if (nameCheck) {
      const confirmNumber = window.confirm(
        ` ${newName} is already added to the phone book, replace old number with a new one?`
      );
      if (confirmNumber) {
        const personFound = persons.find((p) => p.name === newName);
        const changedPerson = { ...personFound, number: newNumber };

        personService
          .update(personFound, changedPerson)
          .then((returnedPerson) => {
            setPersons(
              persons.map((person) => {
                return person.name !== personFound.name
                  ? person
                  : returnedPerson;
              })
            );
          })
          .catch((error) => {
            setErrorMessage(
              `Can't update, this entry is no longer on the server.`
            );
            setTimeout(() => {
              setErrorMessage(null);
            }, 5000);
          });
        setNewName('');
        setNewNumber('');
        setSearchName('');
      }
    } else {
      const nameObject = {
        name: newName,
        number: newNumber,
      };
      personService.create(nameObject).then((returnedPerson) => {
        setPersons(persons.concat(returnedPerson));
        setSuccessMessage(`Added ${returnedPerson.name}`);
        setTimeout(() => {
          setSuccessMessage(null);
        }, 3000);
        setNewName('');
        setNewNumber('');
      }).catch((error) => {
        console.log(error);
        setErrorMessage(
          `Person validation failed ${error}`,
          'error'
        );
        setTimeout(() => {
          setErrorMessage(null);
        }, 5000);
      });
    }
  };

  const handleNameChange = (event) => {
    setNewName(event.target.value);
  };

  const handleNumberChange = (event) => {
    setNewNumber(event.target.value);
  };

  const handleSearchChange = (event) => {
    setSearchName(event.target.value);
  };

  const deletePerson = (id) => {
    const personToDelete = persons.find((person) => person.id === id);
    const confirmDelete = window.confirm(
      `Delete entry: ${personToDelete.name} ? `
    );
    if (confirmDelete) {
      personService
        .remove(id)
        .then((returnedPerson) => {
          setPersons(persons.filter((p) => p.id !== id));
          setSearchName('');
        })
        .catch((error) => {
          console.log(error);
          setErrorMessage(
            `${personToDelete.name} had already been removed`,
            'error'
          );
          setTimeout(() => {
            setErrorMessage(null);
          }, 5000);
        });
    }
  };

  const regexp = new RegExp(searchName, 'i');
  const personsToShow = persons.filter((person) => regexp.test(person.name));

  return (
    <div>
      <h2>Phone book</h2>
      <Notification className="success" message={successMessage} />
      <Notification className="error" message={errorMessage} />

      <Filter searchName={searchName} handleSearchChange={handleSearchChange} />

      <h3>Add a new</h3>
      <PersonForm
        newName={newName}
        newNumber={newNumber}
        handleNameChange={handleNameChange}
        handleNumberChange={handleNumberChange}
        addName={addName}
      />

      <h3>Numbers</h3>

      <div>
        <Persons persons={personsToShow} deletePerson={deletePerson} />
      </div>
    </div>
  );
};

export default App;
